<template>
  <v-form ref="form" @submit.prevent>
    <v-container>
      <v-subheader>Beneficiado</v-subheader>
      <v-row align="center">
        <v-col class="d-flex" cols="12">
          <v-text-field
            v-model="beneficiado.cdUsuario"
            label="Id"
            outlined
            disabled
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row align="center">
        <v-col class="d-flex" cols="12">
          <v-text-field
            v-model="beneficiado.nome"
            :counter="50"
            label="Nome"
            placeholder="Nome do Beneficiado"
            outlined
            required
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row align="center">
        <v-col class="d-flex" cols="12">
          <v-text-field
            v-model="beneficiado.login"
            :counter="50"
            label="Login"
            placeholder="Login do Beneficiado"
            outlined
            required
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row align="center">
        <v-col class="d-flex" cols="12">
          <v-text-field
            v-model="beneficiado.senha"
            :counter="50"
            :type="fldPassword ? 'text' : 'password'"
            :append-icon="fldPassword ? 'mdi-eye' : 'mdi-eye-off'"
            hint="Pelo menos 3 caracteres"
            name="fldSenha"
            label="Senha"
            placeholder="Senha do Beneficiado"
            outlined
            required
            @click:append="fldPassword = !fldPassword"
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row>
        <v-col class="d-flex" cols="12" sm="8">
          <v-text-field
            v-model="beneficiado.rua"
            :counter="50"
            label="Rua"
            placeholder="Nome da Rua do Beneficiado"
            single-line
            outlined
            required
          ></v-text-field>
        </v-col>
        <v-col class="d-flex" cols="12" sm="4">
          <v-text-field
            v-model="beneficiado.numCasa"
            label="Número"
            placeholder="Nº da Casa do Beneficiado"
            type="number"
            single-line
            outlined
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row>
        <v-col class="d-flex" cols="12">
          <v-combobox
          :items="bairros"
          :hint="`${beneficiado.bairro.cidade.nome + ' - ' + beneficiado.bairro.cidade.uf.sigla}`"
          v-model="beneficiado.bairro"
          item-text="nome"
          item-value="id"
          label="Bairro"
          placeholder="Escolha o Bairro"
          return-object
          outlined
          required
        ></v-combobox>
        </v-col>
        <v-col class="d-flex" cols="12">
          <v-slider
            v-model="beneficiado.npf"
            type="number"
            min=1
            max=50
            :value="`${beneficiado.npf}`"
            thumb-label="always"
            label="Nº de Pessoas da Família"></v-slider>
        </v-col>
      </v-row>
      <v-btn color="success" class="" @click="save">Salvar</v-btn>
    </v-container>
  </v-form>
</template>

<script>
import DataService from "../service/BeneficiadoDataService";
import BairroDataService from "../service/BairroDataService";

export default {
  name: "Beneficiado",
  data() {
      return {
          beneficiado: {cdUsuario:0,nome:'',login:'',senha:'',npf:0,rua:'',numCasa:0,bairro:{id:0,nome:'',cidade:{id:0,nome:'',uf:{id:0,sigla:'',nome:''}}}},
          bairros: [],
          fldPassword: false,
          rules: {
            required: value => !!value || 'Required.',
          },
      };
  },
  computed: {
      id() {
          return this.$route.params.id;
      }
  },
  methods: {
      refresh() {
          if(this.id > 0){
              DataService.retrieve(this.id)
                  .then(response => {
                  this.beneficiado = response.data
              });
          }
          BairroDataService.retrieveAll()
              .then(response => {
              this.bairros = response.data
          });
      },
      save(e) {
          e.preventDefault();
          if(this.id > 0){
              DataService.update(this.id, this.beneficiado, {
                  id: this.id,
                  beneficiado: this.beneficiado
              }).then(() => {
                  this.$router.push('/beneficiados')
              }).catch(e => {
                  console.log(e)
                  if(e.response){
                    this.$store.dispatch('setSnackbar', {
                      showing: true, text: e.response.data.message, timeout: 8000, color: 'error'
                    });
                  }
              });
          } else {
              console.log(this.beneficiado);
              DataService.create(this.beneficiado, {
                  beneficiado: this.beneficiado
              }).then(() => {
                  this.$router.push('/beneficiados')
              }).catch(e => {
                  console.log(e)
                  if(e.response){
                    this.$store.dispatch('setSnackbar', {
                      showing: true, text: e.response.data.message, timeout: 8000, color: 'error'
                    });
                  }
              });
          }
      }
  },
  created() {
      this.refresh()
  }
};
</script>

